.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
.App_main{
  min-height: 100vh; 
  background-color: #282c34;
  color: white;
}
.common_header{

}
.common_content{
  min-height: 300px;
  margin:20px 20px 0 20px;
  padding-bottom: 20px;
}
.common_title{
  font-size: calc(10px + 3vmin);
  text-align: center;
  height: 50px;
  line-height: 50px;
}
.common_code_style{
  background: #636363;
  color: #ff66de;
  padding: 4px;
  font-weight: 100;
}
.common_box{
  border:1px solid #eee;
  min-height: 50px;
  margin:0 20px;
  font-size: 16px;
  font-weight: 100;
  padding-bottom: 10px;
}
.common_error_color{
  margin-left: 6px;
  color:#00ff66
}
.btn{
  margin-top: 5px;
  margin-left: 5px;
  padding: 5px;
  border: 1px solid #13ffd1;
  background: #09d3ac;
}
/* 返回顶部 */
#back_top{
   position: fixed;
   bottom: 20px;
   right: 20px;
   padding: 3px 6px;
   background: #5c74a5;
   border-radius: 5px;
   cursor: pointer;
   z-index: 2;
}
#back_top:hover{
  background: #e2f541;
  color: #000;
}
/* 面板标签 */
#label_board{
  position: fixed;
  top:10%;
  right:50px;
  
}
#label_board ul{
  min-width:200px;
  min-height:  calc(50px + 30vmin);
  max-height: 220px;
  overflow-y: auto;
  z-index: 2;
  background: #5c74a5;
  box-shadow: 2px 2px 5px #94aee0; 
  margin-right: -999px;
  padding-right: 999px;
}
#label_board .label_board_span:hover{
  color: #eee !important; 
  box-shadow:rgb(11, 234, 235) 0px 0px 18px inset;
}