.React_Basic{
  font-size: calc(10px + 2vmin); 
}
.React_Basic ul{
  padding: 0 20px;
  margin:0;
  margin-bottom: 6px;
  list-style-type: none;
}
.React_Basic .fold_board{
  position: fixed;
  right: 0;
  top: 0;
  height: 20px;
  padding: 3px 10px;
  background: #d4b000;
  z-index: 3;
  font-size: 14px;
  cursor: pointer;
}
.React_Basic .fold_board:hover{
  background: #00d424;
}

.nav_span:hover{
   background: #147888;
   color: #d0d3ff !important;
}
.title_id{
  cursor: pointer;
  display: inline-block;
}
.title_id:hover{
  color: #1ee98a !important;
}