.markdown-edit{}
.code-box.preview .rc-md-editor .editor-container .sec-md{
   display: none;
}
.code-box.preview .tool-bar{
  display: none;
}
.code-box.preview .rc-md-editor{
  border: none
}
.code-box pre code {
  background: #fff;
  border: none;
}
pre code {
  display: block;
  padding: 16px 32px;
  color: #314659;
  font-size: 14px;
  font-family: 'Lucida Console',Consolas,Monaco,'Andale Mono','Ubuntu Mono',monospace;
  line-height: 2;
  white-space: pre;
  background: white;
  border: 1px solid #e9e9e9;
  border-radius: 2px;
}
.code-box code[class*='language-'], .code-box pre[class*='language-'] {
  color: black;
  font-family: 'Lucida Console',Consolas,Monaco,'Andale Mono','Ubuntu Mono',monospace;
  line-height: 1.5;
  direction: ltr;
  white-space: pre;
  text-align: left;
  word-wrap: normal;
  word-break: normal;
  word-spacing: normal;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
  background: none;
  text-shadow:none;
}