.Wirkung .minHeight{
  min-height: 300px;
}
.Wirkung .banner{
   width: 100%;
   height: 300px;
   display: flex;
   justify-content: center;
   align-items: center;
   position: relative;
}
.Wirkung .banner .name_say{
  margin-top: -25px;
}
.Wirkung .banner span:nth-of-type(1){
  font-size: 28px;
}
.Wirkung .banner .line{
  display: inline-block;
  position: relative;
  top: 30px;
  left: 10px;
}
.Wirkung .banner .line:after{
  content:'';
  position: absolute;
  width: 70px;
  border: none;
  border-bottom:2px solid #fff;
  left: -80px;
  top: 10px;
}
.Wirkung .banner .img{
  position: relative;
  left: 40px;
  top: 40px;
  width: 150px;
  height: 100px;
}
/* 下拉 */
.Wirkung .link_item{
  display: inline-block;
  position: relative;
  margin: 10px 0 0 10px;
}
.Wirkung .App-link{
  position: relative;
}
.Wirkung .link_item:hover a.drop_title{
  color: chocolate;
}
.Wirkung .link_item:hover li{
  color: #fff;
}
.Wirkung ul{
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.Wirkung .dropmenu{
  position: absolute;
  top: 20px;
  padding-top: 4px;
  min-width: 100%;
  background: transparent;
  max-height: 150px;
  overflow: auto
}
.Wirkung .dropmenu ul li{ 
  color: #fff;
  white-space: pre;
  background: #2aad89;
}
.Wirkung .dropmenu ul li:hover{
  color: #fff934;
}
.Wirkung .dropmenu ul li a{
  color: #fff;
  text-decoration:none;
}
.Wirkung .dropmenu ul li:hover a{
  color: #fff934;
}
.Wirkung .dropmenu ul li.dropmenu_item a{
   display: block;
   padding: 10px;
}